@font-face {
  font-family: "Inria";
  src:
    url("./fonts/Archivo-Medium.otf") format("opentype"),
}

@font-face {
  font-family: "Inria-Bold";
  src:
    url("./fonts/Archivo-Bold.otf") format("opentype"),

}

@font-face {
  font-family: "Inria-Regular";
  src:
    url("./fonts/Archivo-Regular.otf") format("opentype"),

}

/*GENERAL*/

body {
  font-family: "Inria";
  overflow: hidden;
}

.section {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#canvasContainer {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100dvh;
  z-index: 1;
}

/*NAVBAR*/

#navBarHome {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  height: 100px;
}

.linkNav {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border: black solid 1px;
  width: 20vw;
  height: 40px;
  font-weight: bold;
}

.linkNav:hover {
  background-color: black;
  color: white;
  border: transparent solid 1px;
}

#navBarHome a {
  color: black;
  text-decoration: none;
  font-size: 18px;
  z-index: 2
}

#homeLogo {
  width: 300px;
  height: 150px;
  background-size: 100% 100%;
}

/*TALENT*/

#talentTitle {
  position: absolute;
  left: 50%;
  top: 37%;
  transform: translateX(-50%);
  color: black;
  transition: 1s cubic-bezier(.07, .77, .44, .99);
  font-size: 20px;
  font-weight: normal;
  font-family: "Inria-Regular";

}

#talent {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  transition: 1s cubic-bezier(.05, .7, .3, 1);
}

#talent h1 {
  cursor: pointer;
  margin: 0;
  font-weight: normal;
}

#talent h1:hover {
  text-decoration: underline;
}

#talent span {
  color: red;
  font-weight: bold;
}

#talentImages {
  border: 1px solid black;
  position: absolute;
  left: 12.5%;
  top: 38%;
  height: 40%;
  width: 60%;
  display: none;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  opacity: 0;
  transition: .3s;
}

.talentImage {
  height: 100%;
  display: inline-block;
  margin: 0;
}

#talentContacts {
  position: absolute;
  top: 38%;
  left: 74%;
  height: 40%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  display: none;
  opacity: 0;
}

.talentContact {
  color: black;
  width: 13.3vw;
  height: 5vh;
  border: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  text-decoration: none;
  font-family: "Inria-Bold";
  margin: 0 0 20px 0;
}

.talentContact:hover {
  color: white;
  background-color: black;
}

/*BRANDS*/

#brands {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 70px;
  display: flex;
  overflow: hidden;
  background-color: black;
}

.ticker-track {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: unset;
  flex-grow: 0;
  flex-shrink: 0;
  animation: ticker 30s linear infinite;
  -webkit-animation: ticker 30s linear infinite;
  animation-play-state: paused;
  -webkit-animation-play-state: paused;
}

.ticker-img {
  width: auto;
  height: 50%;
  padding: 0 1.5rem;
  filter: invert(1);
}

@keyframes ticker {
  0% {
    transform: translateX(0%);
  }

  100% {
    transform: translateX(-100%);
  }
}

@-webkit-keyframes ticker {
  0% {
    transform: translateX(0%);
  }

  100% {
    transform: translateX(-100%);
  }
}

/*SPOTIFY RADIO*/

#spotifyContainer {
  position: absolute;
  right: 0;
  bottom: -383px;
  width: 25%;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: .75s;
}

#spotifyOpen {
  display: flex;
  width: 100%;
  height: 70px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

#spotifyOpen h1 {
  font-size: 25px;
}

#spotifyArrow {
  width: auto;
  height: 25px;
  margin-right: 10px;
}

#spotifyIntegration {
  background-color: #535353;
}

/*ABOUT*/

#aboutText {
  z-index: 2;
  position: absolute;
  text-align: center;
  left: 50%;
  top: 42%;
  transform: translate(-50%, -50%);
  color: black;
  width: 50%;
  font-size: 18px;
}

#contactInfo {
  z-index: 2;
  position: absolute;
  top: 57%;
  left: 50%;
  width: 56.3%;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-between;
}

.contacts {
  width: 13.3vw;
  height: 5vh;
  border: 1px black solid;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Inria-Bold";
}

.contacts:hover {
  cursor: pointer;
  color: white;
  background-color: black;
}

.credits {
  z-index: 2;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  color: black;
  font-size: 12px;
}

.credits a {
  color: black;
  font-family: "Inria-Bold";
  text-decoration: none;
}

.credits a:hover {
  text-decoration: underline;
}

/*GALLERY*/

#cursor {
  position: absolute;
  transform: translate(-50%, -50%);
  color: white;
  font-family: "Inria-Bold";
}